import PointListView from '../view/point-list-view.js'; //  ul
import NoPointView from '../view/no-point-view.js';
import SortView from '../view/sort-view.js';
import PointPresenter from './point-presenter.js';
import {render, RenderPosition, remove} from '../framework/render.js';
import {UpdateType, SortType, UserAction} from '../const.js';
import {sortPointsByDuration, sortPointsByPrice, sortPointsByDate} from '../utils/point.js';


export default class PointsListPresenter {
  #pointListComponent = new PointListView(); //  ul  point,  
  #sortComponent = null;
  #noPointComponent = new NoPointView();
  #presenterContainerElement = null; // DOM-,    
  #pointsModel = null;

  #allPointPresenters = new Map();

  #currentSortType = SortType.DAY; //   

  //         :
  //  -  (DOM-!),    !
  //  -   
  constructor({presenterContainerElement, pointsModel}) {
    this.#presenterContainerElement = presenterContainerElement; //  DOM-,      ,    
    this.#pointsModel = pointsModel;

    this.#pointsModel.addObserver(this.#handleModeEvent);
  }

  get points() { //           
    switch (this.#currentSortType) {
      case SortType.DAY:
        return [...this.#pointsModel.points].sort(sortPointsByDate);
      case SortType.TIME:
        return [...this.#pointsModel.points].sort(sortPointsByDuration);
      case SortType.PRICE:
        return [...this.#pointsModel.points].sort(sortPointsByPrice);
    }
    return this.#pointsModel.points;
  }

  get destinations() {
    return this.#pointsModel.destinations;
  }

  get offers() {
    return this.#pointsModel.offers;
  }

  //       
  #renderPoint({point}) {
    const pointPresenter = new PointPresenter({
      pointListContainer: this.#pointListComponent.element,
      onDataChange: this.#handleViewAction,
      onModeChange: this.#handleModeChange
    });
    pointPresenter.init(point, this.destinations, this.offers);
    this.#allPointPresenters.set(point.id, pointPresenter);
  }

  //      
  #renderSort() {
    this.#sortComponent = new SortView({
      sortType: this.#currentSortType,
      onSortTypeChange: this.#handleSortTypeChange
    });

    render(this.#sortComponent, this.#presenterContainerElement, RenderPosition.AFTERBEGIN);
  }

  //       
  #renderNoPoints() {
    render(this.#noPointComponent, this.#presenterContainerElement);
  }

  #renderBoard() {
    const pointCount = this.points.length;

    if(!pointCount) {
      this.#renderNoPoints();
    } else {
      this.#renderSort();
      render(this.#pointListComponent, this.#presenterContainerElement); //   ul
      for (let i = 0; i < this.points.length; i++) {
        this.#renderPoint({point: this.points[i]});
      }
    }
  }

  //       
  #clearBoard({resetSortType = false} = {}) {
    this.#allPointPresenters.forEach((presenter) => presenter.destroy());
    this.#allPointPresenters.clear();

    remove(this.#sortComponent);
    remove(this.#noPointComponent);

    if (resetSortType) {
      this.#currentSortType = SortType.DEFAULT;
    }
  }

  init() {
    this.#renderBoard();
  }

  //  ,     - ,     
  #handleViewAction = (actionType, updateType, update) => {
    //console.log({actionType, updateType, update});
    switch (actionType) {
      case UserAction.UPDATE_POINT:
        this.#pointsModel.updatePoint(updateType, update);
        break;
      case UserAction.ADD_POINT:
        this.#pointsModel.addPoint(updateType, update);
        break;
      case UserAction.DELETE_POINT:
        this.#pointsModel.deletePoint(updateType, update);
        break;
    }
  };

  #handleModeEvent = (updateType, data) => {
    switch (updateType) {
      case UpdateType.PATCH:
        this.#allPointPresenters.get(data.id).init(data, this.destinations, this.offers); //   , ,  
        break;
      case UpdateType.MINOR:
        this.#clearBoard(); //   (, / )
        this.#renderBoard();
        break;
      case UpdateType.MAJOR:
        this.#clearBoard({resetSortType: true}); //  ,  
        this.#renderBoard();
        break;
    }
  };

  #handleModeChange = () => {
    this.#allPointPresenters.forEach((presenter) => presenter.resetView());
  };

  #handleSortTypeChange = (sortType) => {
    if(this.#currentSortType === sortType) {
      return;
    }
    this.#currentSortType = sortType;
    this.#clearBoard();
    this.#renderBoard();
  };
}
